                `Pero antes de que hayamos tenido tiempo para adentrarse en la contemplación, Lucio nos instruye para observar la biología de lo que parece ser un terreno completamente desolado. Nos muestra una colonia próspera de líquenes en las rocas aisladas a esta zona cercana. Por último, observamos una extraña floración, aparentemente de la nada, al final de los agujeros antes de la cumbre. Después de haber pasado los últimos kilómetros de terreno desertico es difícil de comprender cómo sobrevive esta flor.`
