                `Lucio continúa describiendo Q'hoa: "Él tomó muchas formas, pero lo representan de manera compatible con un cuerpo que comprende los círculos concéntricos que representan gotas de agua. Su misión eran las lluvias que trajo el agua que da vida a los agricultores, mientras que la cara es a menudo la de un felino, su cuerpo se hizo largo y con forma de serpiente, a veces un homenaje a los arco iris, otras veces un tributo a los ríos que se llenan de sangre después de las lluvias que trajeron. En algunas representaciones tenía alas y patas y mucho más tarde tomó la forma de un águila”.`
